import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useProjectInfoByHandle } from '../../hooks/useProjectInfo';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import styles from './ProjectDesign.module.scss';
import DesignForm from '../../components/ProjectDesign/DesignForm/DesignForm';
import { validateProject } from '../../utils/utils';
import { useWallet } from '../../hooks/useWallet';

const ProjectDesign = () => {
  const { t } = useTranslation();
  const { projectHandle } = useParams();
  const history = useHistory();
  const { loading, projectInfo } = useProjectInfoByHandle(projectHandle);
  const [projectData, setProjectData] = useState();
  const { account } = useWallet();

  useEffect(() => {
    const testProject = async () => {
      if (!projectHandle) {
        return;
      }
      if (!loading && projectInfo) {
        if (
          validateProject(projectInfo) &&
          account &&
          projectInfo.projectOwner.toLowerCase() === account
        ) {
          setProjectData(projectInfo);
        } else {
          history.push('/design');
        }
      }
    };
    testProject().catch((e) => {
      console.log(e);
    });
  }, [projectHandle, account, projectInfo, loading]);

  return (
    <div className={styles.ProjectDesign}>
      <section
        className={styles.banner}
        style={{ backgroundImage: 'url(/static/images/projectDesign/bannerBg.png)' }}>
        <MainContainer className={styles.bannerContainer}>
          <h3>{t('deseign-welcome')} </h3>
          <h2>{t('design-your-project')} </h2>
        </MainContainer>
      </section>

      <MainContainer>
        <DesignForm project={projectData} />
      </MainContainer>
    </div>
  );
};

export default ProjectDesign;
