import { useEffect, useState, useCallback } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Home from './views/Home/Home';
import Projects from './views/Projects/Projects';
import ProjectDetail from './views/ProjectDetail/ProjectDetail';
import ProjectDesign from './views/ProjectDesign/ProjectDesign';
import { useWallet } from './hooks/useWallet';
import './App.scss';

function App() {
  const { ethersProvider } = useWallet();
  const [blockNumber, setBlockNumber] = useState(0);

  const blockNumberCallback = useCallback(
    (newBlockNumber) => {
      setBlockNumber((bn) => {
        if (bn !== newBlockNumber) {
          return newBlockNumber;
        }
        return bn;
      });
    },
    [ethersProvider],
  );

  useEffect(() => {
    ethersProvider.on('block', blockNumberCallback);
    return () => {
      ethersProvider.removeListener('block', blockNumberCallback);
    };
  }, [ethersProvider, blockNumber]);
  return (
    <div className="App">
      <Header />
      <div className="mainContentWrap">
        <Switch>
          <Route path="/" exact>
            <Home />
          </Route>
          <Route path="/projects" exact>
            <Projects />
          </Route>
          <Route path="/projects/:projectHandle" exact>
            <ProjectDetail />
          </Route>
          <Route path="/design" exact>
            <ProjectDesign />
          </Route>
          <Route path="/design/:projectHandle">
            <ProjectDesign />
          </Route>
          <Redirect to="/" />
        </Switch>
      </div>
      <Footer />
    </div>
  );
}

export default App;
