import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import classnames from 'classnames';
import numeral from 'numeral';
import { utils, constants } from 'ethers';
import { Input, message } from 'antd';
import { RightOutlined } from '@ant-design/icons';
import MainButton from '../../MainButton/MainButton';
import DetailPage from '../DetailPage/DetailPage';
import MintInput from './MintInput/MintInput';
import { useGoFundUsManagerContract, useTokenContract } from '../../../hooks/useContract';
import { useWallet } from '../../../hooks/useWallet';
import { useAccountNativeBalance, useAccountTokenBalance } from '../../../hooks/useAccountBalance';
import styles from './ProjectAction.module.scss';
import { TOKENS } from '../../../constants/addresses';
import { useUpdate } from '../../../hooks/useUpdate';

const ProjectAction = (props) => {
  const { t } = useTranslation();
  const { className, item } = props;
  const [inputValue, setInputValue] = useState(item.minimumDonationAmount);
  const { data: balance } =
    item.donationToken === TOKENS.BNB
      ? useAccountNativeBalance()
      : useAccountTokenBalance(item.donationToken);
  const [mintAddress, setMintAddress] = useState('');
  const [mintAmount, setMintAmount] = useState(0);
  const [allowance, setAllowance] = useState(
    item.donationToken === '0x0000000000000000000000000000000000000000' ? 1 : 0,
  );

  const gfuManagerContract = useGoFundUsManagerContract();
  const tokenContract = useTokenContract(item.donationToken);
  const { account } = useWallet();
  const { updateByBlockNumber } = useUpdate();

  async function getAllowance() {
    if (
      !account ||
      !tokenContract ||
      !tokenContract.allowance ||
      !gfuManagerContract ||
      !gfuManagerContract.address ||
      allowance > 0
    ) {
      return;
    }
    const tokenAllowance = await tokenContract.allowance(account, gfuManagerContract.address);
    setAllowance(utils.formatEther(tokenAllowance));
  }
  useEffect(() => {
    getAllowance()
      .then()
      .catch((e) => {
        console.log(e);
      });
  }, [
    account,
    item.donationToken,
    gfuManagerContract,
    tokenContract,
    allowance,
    updateByBlockNumber,
  ]);

  async function donate() {
    if (!account || !gfuManagerContract || !gfuManagerContract.donate) {
      message.info('Please connect your wallet');
      return;
    }
    if (parseFloat(utils.formatEther(balance)) < parseFloat(inputValue)) {
      message.info(t('project-action-message-insufficient-balance'));
      return;
    }
    if (parseFloat(item.minimumDonationAmount) > parseFloat(inputValue)) {
      message.info(
        `${t('project-action-message-min-amount')} ${numeral(item.minimumDonationAmount).format(
          '0.00',
        )}`,
      );
      return;
    }
    let inviter = '0xFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF';
    const urlParams = new URLSearchParams(window.location.hash.split('?')[1]);
    const affAddr = urlParams.get('ref');
    const curInviter = item.donorInfo.inviter;
    if (curInviter !== '0x0000000000000000000000000000000000000000') {
      inviter = curInviter;
    } else if (utils.isAddress(affAddr)) {
      inviter = affAddr;
    }
    try {
      const tx = await gfuManagerContract.donate(
        utils.formatBytes32String(item.id),
        utils.parseEther(inputValue),
        inviter,
        {
          value:
            item.donationToken === '0x0000000000000000000000000000000000000000'
              ? utils.parseEther(inputValue)
              : 0,
        },
      );
      tx.wait()
        .then((receipt) => {
          console.log(receipt);
          message.success(t('project-action-message-donation-sent'));
        })
        .catch((e) => {
          console.log(e);
        });
    } catch (e) {
      console.log(e);
    }
  }

  async function endFunding() {
    if (!account || !gfuManagerContract || !gfuManagerContract.finish) {
      message.info('Please connect your wallet');
      return;
    }
    const deadline = Math.floor(+new Date() / 1000 + 30);
    const tx = await gfuManagerContract.finish(utils.formatBytes32String(item.id), deadline);
    tx.wait()
      .then((receipt) => {
        console.log(receipt);
        message.success(t('project-action-message-funding-ended'));
      })
      .catch((e) => {
        console.log(e);
      });
  }

  async function approve() {
    if (!account || !tokenContract || !tokenContract.approve) {
      message.info('Please connect your wallet');
      return;
    }
    const tx = await tokenContract.approve(gfuManagerContract.address, constants.MaxUint256);
    tx.wait()
      .then((receipt) => {
        console.log(receipt);
        message.success(t('project-action-message-transcation-sent'));
        getAllowance().then();
      })
      .catch((e) => {
        console.log(e);
      });
  }

  async function closeMintable() {
    if (!account || !gfuManagerContract || !gfuManagerContract.closeMintable) {
      message.info('Please connect your wallet');
      return;
    }
    const tx = await gfuManagerContract.closeMintable(utils.formatBytes32String(item.id));
    tx.wait()
      .then((receipt) => {
        console.log(receipt);
        message.success(t('project-action-message-transcation-sent'));
        getAllowance().then();
      })
      .catch((e) => {
        console.log(e);
      });
  }

  async function mintToken() {
    if (!account || !gfuManagerContract || !gfuManagerContract.mint) {
      message.info('Please connect your wallet');
      return;
    }
    if (!utils.isAddress(mintAddress)) {
      message.info(t('funding-configuration-error-invalid-address'));
      return;
    }
    const tx = await gfuManagerContract.mint(
      utils.formatBytes32String(item.id),
      mintAddress,
      utils.parseEther(mintAmount),
    );
    tx.wait()
      .then((receipt) => {
        console.log(receipt);
        message.success(t('project-action-message-transcation-sent'));
        getAllowance().then();
      })
      .catch((e) => {
        console.log(e);
      });
  }

  function onInputChange(e) {
    if (Number.isNaN(e.target.value)) {
      return;
    }

    if (Number(e.target.value) > Number(item.amountLeft)) {
      setInputValue(item.amountLeft);
    } else {
      setInputValue(e.target.value);
    }
  }

  function onMintAddressChange(e) {
    setMintAddress(e.target.value);
  }

  function onMintAmountChange(e) {
    if (Number.isNaN(e.target.value * 1)) {
      return;
    }
    console.log(e.target.value);
    setMintAmount(e.target.value);
  }

  return (
    <div className={classnames(styles.ProjectAction, className)}>
      <div className={styles.headerWrap}>
        {/* <div className={styles.titleTabWrap}> */}
        <h2>{t('prject-action-project-details')}</h2>
        {/* <div className={styles.tabWrap}>
            <div
              className={
                detailTab === DETAIL_TAB.DETAIL ? classnames(styles.tab, styles.active) : styles.tab
              }
              onClick={() => setDetailTab(DETAIL_TAB.DETAIL)}>
              {t('prject-action-project-details-details')}
            </div>
            <div
              className={
                detailTab === DETAIL_TAB.DONATION
                  ? classnames(styles.tab, styles.active)
                  : styles.tab
              }
              onClick={() => setDetailTab(DETAIL_TAB.DONATION)}>
              {t('prject-action-project-details-donation-list')}
            </div>
          </div> */}
        {/* </div> */}
        <div className={styles.inputWrap}>
          <div className={styles.inputGroup}>
            <Input
              suffix={
                <span className={styles.inputSuffix}>
                  {item.donationTokenName.toUpperCase()}
                  <RightOutlined className={styles.rightArrow} />
                </span>
              }
              placeholder="0"
              onChange={(e) => {
                onInputChange(e);
              }}
              value={inputValue}
            />
            {item.active &&
              (parseFloat(allowance) > 0 ? (
                <MainButton
                  className={styles.button}
                  onClick={() => {
                    donate().then();
                  }}>
                  {item.description.payButtonText
                    ? item.description.payButtonText
                    : t('prject-action-project-details-contribute')}
                </MainButton>
              ) : (
                <MainButton
                  className={styles.button}
                  onClick={() => {
                    approve().then();
                  }}>
                  {t('project-action-approve')}
                </MainButton>
              ))}
            {!item.active && (
              <MainButton className={classnames(styles.button, styles.inactive)}>
                {t('project-end')}
              </MainButton>
            )}
          </div>
          <div className={styles.exchangeGroup}>
            <div className={styles.exchangeInfoWrapper}>
              <span className={styles.exchangeInfo}>
                <p className={styles.title}>{t('prject-action-project-details-received')}</p>
                <p className={styles.value}>
                  {numeral(item.price.toString()).format('0,0')} {item.tokenSymbol}/
                  {item.donationTokenName}
                </p>
              </span>
              <span className={styles.exchangeInfo}>
                <p className={styles.title}>{t('prject-action-project-details-get')}</p>
                <p className={styles.value}>
                  <a
                    href="https://pancakeswap.finance/swap"
                    target="_blank"
                    rel="noopener noreferrer">
                    {item.donationTokenName}
                  </a>
                </p>
              </span>
            </div>
            {item.isOwner && item.donationDeadline === 0 ? (
              <MainButton
                className={classnames(styles.button, styles.endButton)}
                onClick={() => {
                  endFunding();
                }}>
                {t('project-action-end-funding')}
              </MainButton>
            ) : null}
          </div>
        </div>
      </div>
      <div className={styles.mainWrap}>
        <DetailPage item={item} />
      </div>
      <br />
      {item.mintable && !item.active && item.isOwner ? (
        <div className={styles.mintInfo}>
          <div className={classnames(styles.mintTab, styles.active)}>{t('detail-page-mint')}</div>
          <div className={styles.mintDetail}>
            <div className={styles.inputGroup}>
              <MintInput
                text={t('detail-page-address')}
                placeholder={t('detail-page-mint-address-placeholder')}
                value={mintAddress}
                onChangeCallBack={(e) => {
                  onMintAddressChange(e);
                }}
              />
              <MintInput
                text={t('detail-page-mint-amount')}
                placeholder="0"
                value={mintAmount}
                onChangeCallBack={(e) => {
                  onMintAmountChange(e);
                }}
              />
            </div>

            <div className={styles.buttonGroup}>
              <MainButton
                onClick={() => {
                  mintToken().then();
                }}
                className={classnames(styles.button, styles.endButton, styles.mintButton)}>
                {t('detail-page-mint')} {item.tokenSymbol}
              </MainButton>
              <p
                onClick={() => {
                  closeMintable().then();
                }}>
                {t('project-action-give-up-mint-privilege')}
              </p>
            </div>
          </div>
        </div>
      ) : null}
    </div>
  );
};

ProjectAction.propTypes = {
  item: PropTypes.objectOf(PropTypes.any).isRequired,
  className: PropTypes.string,
};

ProjectAction.defaultProps = { className: '' };

export default ProjectAction;
