import React from 'react';

import styles from './MediaList.module.scss';
import medias from '../../constants/medias';

const MediaList = () => (
  <div className={styles.MediaList}>
    {medias.map((media) => (
      <a href={media.url} target="_blank" rel="noopener noreferrer" key={media.name}>
        <img src={`/static/images/medias/${media.logo}`} alt={media.name} />
      </a>
    ))}
  </div>
);

export default MediaList;
