import React from 'react';
import PropTypes from 'prop-types';
import { message } from 'antd';
import { useTranslation } from 'react-i18next';
import numeral from 'numeral';
import { utils } from 'ethers';
import classnames from 'classnames';
import styles from './DetailPage.module.scss';
import MainButton from '../../MainButton/MainButton';
import { useAccountNativeBalance, useAccountTokenBalance } from '../../../hooks/useAccountBalance';
import { useGoFundUsManagerContract } from '../../../hooks/useContract';
import { TOKENS } from '../../../constants/addresses';
import { checkTime } from '../../../utils/utils';
import Countdown from '../../Countdown/Countdown';

const DetailPage = (props) => {
  const { t } = useTranslation();
  const { item } = props;
  const gfuManagerContract = useGoFundUsManagerContract();
  const { data: tokenBalance } =
    item.donationToken === TOKENS.BNB
      ? useAccountNativeBalance()
      : useAccountTokenBalance(item.donationToken);
  const { data: targetBalanceRaw } = useAccountTokenBalance(item.targetToken);
  const targetBalance = utils.formatEther(targetBalanceRaw);

  async function distributeToken() {
    if (!gfuManagerContract && gfuManagerContract.takeTarget) {
      return;
    }
    const tx = await gfuManagerContract.takeTarget(utils.formatBytes32String(item.id));
    tx.wait()
      .then((receipt) => {
        console.log(receipt);
        message.success(t('detail-page-token-distributed'));
      })
      .catch((e) => {
        console.log(e);
      });
  }

  async function claim(isBurn = false) {
    if (!gfuManagerContract && gfuManagerContract.claim) {
      return;
    }
    const tx = await gfuManagerContract.claim(utils.formatBytes32String(item.id), isBurn);
    tx.wait()
      .then((receipt) => {
        console.log(receipt);
        message.success(t('project-action-message-transcation-sent'));
      })
      .catch((e) => {
        console.log(e);
      });
  }

  return (
    <div className={styles.DetailPage}>
      <div className={styles.statsWrap}>
        <div className={styles.generalInfo}>
          <span className={styles.infoItem}>
            <span className={styles.title}>{t('detail-page-volume')}</span>
            <span className={styles.valueWrap}>
              <img
                src={`/static/images/tokens/${item.donationTokenName.toLowerCase()}.svg`}
                alt="logo"
              />
              <span className={styles.mainValue}>
                {numeral(item.totalDonation).format('0,0.[00]')}
              </span>
            </span>
          </span>
          <span className={styles.infoItem}>
            <span className={styles.title}>{t('project-actionp-articipants')}</span>
            <span className={styles.valueWrap}>
              <img src="/static/images/icons/user.svg" alt="logo" />
              <span className={styles.mainValue}>{numeral(item.numberOfDonors).format('0,0')}</span>
            </span>
          </span>
          <span className={styles.infoItem}>
            <span className={styles.title}>{t('detail-page-in-gofundus')}</span>
            <span className={styles.valueWrap}>
              <img
                src={`/static/images/tokens/${item.donationTokenName.toLowerCase()}.svg`}
                alt="logo"
              />
              <span className={styles.mainValue}>
                {numeral(item.donorInfo.donationAmount).format('0,0.[00]')}
              </span>
            </span>
          </span>
          <span className={styles.infoItem}>
            <span className={styles.title}>{t('detail-page-in-wallet')}</span>
            <span className={styles.valueWrap}>
              <span className={styles.secondaryValue}>
                {numeral(targetBalance).format('0,0')} {item.tokenSymbol} +
              </span>
              <img
                src={`/static/images/tokens/${item.donationTokenName.toLowerCase()}.svg`}
                alt="logo"
              />
              <span className={styles.mainValue}>
                {numeral(utils.formatEther(tokenBalance)).format('0,0.[00]')}
              </span>
            </span>
          </span>
        </div>
        <div className={styles.tokenWrap}>
          <div className={classnames(styles.balanceWrap, styles.grayBox)}>
            <div className={styles.balanceWrapHeader}>
              <div>
                <h2>{t('detail-page-your-balance')}</h2>
                <span>
                  <h2>
                    {numeral(
                      item.donorInfo.claimWithBurned ? 0 : item.donorInfo.claimableAmount,
                    ).format('0,0')}{' '}
                  </h2>
                  <p>{item.tokenSymbol}</p>
                </span>
              </div>
              {!item.active && item.donorInfo.claimable && item.donorInfo.claimableAmount > 0 ? (
                <div className={styles.buttonGroup}>
                  <MainButton
                    className={styles.button}
                    onClick={() => {
                      claim().then();
                    }}>
                    {t('detail-page-claim')}
                  </MainButton>
                  <MainButton
                    className={styles.button}
                    onClick={() => {
                      claim(true).then();
                    }}>
                    {t('detail-page-burn')}
                  </MainButton>
                </div>
              ) : null}
            </div>

            <hr />
            <div className={styles.claimable}>
              <span>
                <h2>{t('detail-page-claimabble')}:</h2>
                <p>
                  {item.donorInfo.claimable
                    ? numeral(item.donorInfo.claimableAmount).format('0,0.[00]')
                    : 0}
                </p>
              </span>
              <span>
                <h2>{t('detail-page-of-supply')}:</h2>
                <p>
                  {item.totalDonation * item.price === 0
                    ? '0%'
                    : numeral(
                        item.donorInfo.claimableAmount / (item.totalDonation * item.price),
                      ).format('0,0.[0000]%')}
                </p>
              </span>
            </div>
          </div>
        </div>
      </div>
      <div className={styles.divider} />
      <div className={styles.actionWrap}>
        <h2 style={{ marginBottom: 10 }}>{item.tokenSymbol} (BEP-20)</h2>
        <span className={styles.infoItem}>
          <span className={styles.title}>{t('detail-page-address')}</span>
          <span className={styles.valueWrap} style={{ display: 'block' }}>
            <a
              href={`https://bscscan.com/token/${item.targetToken}`}
              target="_blank"
              rel="noopener noreferrer">
              <span className={styles.secondaryValue}>{item.targetToken}</span>
            </a>
          </span>
        </span>
        <span className={styles.infoItem}>
          <span className={styles.title}>{t('detail-page-total-supply')}</span>
          <span className={styles.valueWrap}>
            <span className={styles.secondaryValue}>
              {numeral(item.totalDonation * item.price).format('0,0.[00]')} {item.tokenSymbol}
            </span>
          </span>
        </span>
        <br />
        <div className={styles.fundingCycleHeaderWrap}>
          <h2>{t('detail-page-funding-cycle')}</h2>
          <br />
          {/* eslint-disable-next-line no-nested-ternary */}
          {(item.donationDeadline > 0 && checkTime(item.donationDeadline * 1000)) ||
          !item.active ? (
            <div className={styles.statsTag}>{t('project-end')}</div>
          ) : item.donationDeadline === 0 ? null : (
            <Countdown timestamp={item.donationDeadline * 1000} className={styles.countdown} />
          )}
        </div>

        <span className={styles.infoItem}>
          <span className={styles.title}>
            {t('detail-page-reserved')} {item.tokenSymbol}
          </span>
          <span className={styles.valueWrap}>
            <span className={styles.secondaryValue}>
              {numeral(item.reservePercent).format('0.[00]')}%
            </span>
          </span>
        </span>
        <div className={classnames(styles.fundWrap, styles.grayBox)}>
          <div className={styles.statsWrap}>
            <span className={styles.infoItem}>
              <span className={styles.title}>
                {t('detail-page-reserved')}({item.tokenSymbol})
              </span>
              <span className={styles.valueWrap}>
                <span className={styles.secondaryValue}>
                  {numeral((item.reservePercent * item.totalDonation * item.price) / 100).format(
                    '0,0.[00]',
                  )}
                </span>
              </span>
            </span>
          </div>
          {!item.active && Number(item.reservePercent) > 0 && !item.targetTaken ? (
            <MainButton
              className={styles.distributionButton}
              onClick={() => {
                distributeToken().then();
              }}>
              {t('detail-page-distribute')} {item.tokenSymbol}
            </MainButton>
          ) : null}
        </div>
      </div>
    </div>
  );
};

DetailPage.propTypes = {
  item: PropTypes.objectOf(PropTypes.any).isRequired,
};

export default DetailPage;
