import React, { useCallback, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useHistory } from 'react-router-dom';
import { ethers } from 'ethers';
import { Form, message, Modal } from 'antd';
import { useTranslation } from 'react-i18next';
import classnames from 'classnames';
import styles from './DesignForm.module.scss';
import MainButton from '../../MainButton/MainButton';
import NavItem from '../NavItem/NavItem';
import FORM_CONFIG from './formConfig';
import FORM_STATE from './formState';
import { useWallet } from '../../../hooks/useWallet';
import { useGoFundUsManagerContract } from '../../../hooks/useContract';
import { encodeObjectToBytes, TIME_IN_SECOND, getTokenAddress } from '../../../utils/utils';
import { SUPPORT_TOKEN } from '../../../utils/projectsConfig';

const DesignForm = (props) => {
  const { project } = props;
  const formCount = FORM_CONFIG.length;
  const [formState, setFormState] = useState(new Array(formCount).fill(FORM_STATE.NOTSTARTED));
  const [currentStep, setCurrentStep] = useState(0);
  const [currentForm, setCurrentForm] = useState();
  const [allComplete, setAllComplete] = useState(false);
  const [form] = Form.useForm();
  const history = useHistory();
  const { t } = useTranslation();

  const { account } = useWallet();
  const managerContract = useGoFundUsManagerContract();
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [editing, setEditing] = useState(false);

  const updateArray = (stateHandler, itemIndex, itemValue) => {
    stateHandler((prevState) =>
      prevState.map((item, index) => (itemIndex === index ? itemValue : item)),
    );
  };

  // validate current mounted form
  const validateForm = async () => {
    try {
      await form.validateFields();
      updateArray(setFormState, currentStep, FORM_STATE.COMPLETED);
      return true;
    } catch (err) {
      updateArray(setFormState, currentStep, FORM_STATE.INPROGRESS);
      return false;
    }
  };

  const nextStep = async () => {
    if (editing) {
      return;
    }
    await validateForm();
    setCurrentStep((step) => Math.min(formCount - 1, step + 1));
  };

  const prevStep = async () => {
    if (editing) {
      return;
    }
    await validateForm();
    setCurrentStep((step) => Math.max(0, step - 1));
  };

  const goStep = async (step) => {
    if (editing) {
      return;
    }
    if (step < 0 || step >= formCount) {
      return;
    }
    await validateForm();
    setCurrentStep(step);
  };

  // find index of incomplete form other than provided index
  const findIncompleteExcludeSelf = (excludeIndex) =>
    formState.findIndex((item, index) => item !== FORM_STATE.COMPLETED && index !== excludeIndex);

  // find index of first incomplete/notstarted form
  const findIncomplete = useCallback(
    () => formState.findIndex((item) => item !== FORM_STATE.COMPLETED),
    [formState],
  );

  const getDefaultExchangeRate = (tokenSymbol) => {
    switch (tokenSymbol) {
      case SUPPORT_TOKEN.BNB:
        return '1000000';
      case SUPPORT_TOKEN.BUSD:
        return '10000';
      default:
        return null;
    }
  };

  const createProject = async () => {
    if (!account) {
      message.info(t('design-form-message-please-connect-to-wallet'));
      return;
    }

    const data = form.getFieldsValue(true);
    console.log('raw param', data);
    try {
      const param = {
        id: ethers.utils.formatBytes32String(data.projectHandle), // REQUIRED project handle
        projectName: data.projectName, // REQUIRED project name
        tokenName: data.tokenName, // REQUIRED token name
        tokenSymbol: data.tokenSymbol, // REQUIRED token symbol
        description: encodeObjectToBytes({
          // project description: REQUIRED logo URL
          projectDesc: data.projectDesc,
          website: data.website,
          telegram: data.telegram,
          twitter: data.twitter,
          discord: data.discord,
          payButtonText: data.payButtonText,
          logoUrl: data.logoUrl,
        }),
        donationDeadline: data.fundingDuration
          ? Math.floor(Date.now() / 1000) + data.fundingDuration * TIME_IN_SECOND.MINUTE
          : 0,
        donationToken: getTokenAddress(data.acceptToken), // REQUIRED accepted currency
        donationDistribution: data.fundDistribution
          ? data.fundDistribution.map((item) => ({
              who: item.address,
              perMillion: item.ratio * 10000,
              unlockTimestamp: 0,
              processed: false,
            }))
          : [],
        minimumDonationAmount: ethers.utils.parseEther(data.minAmountPerTxn), // REQUIRED min amount
        donationCap: data.hardCap ? ethers.utils.parseEther(data.hardCap) : 0,
        supportOverDonation: false,
        price: data.exchangeRate ? data.exchangeRate : getDefaultExchangeRate(data.acceptToken),
        reservePerMillion: data.reservedRatio ? data.reservedRatio * 10000 : 0,
        reserveDistribution: data.tokenReserve
          ? data.tokenReserve.map((item) => ({
              who: item.address,
              perMillion: item.ratio * 10000,
              unlockTimestamp: 0,
              processed: false,
            }))
          : [],
        mintable: data.isMintable,
      };

      console.log('encoded param', param);
      const tx = await managerContract.createProject(param);
      console.log('tx', tx);
      message.success(t('deseign-form-submitted'));
      const receipt = await tx.wait();
      console.log('receipt', receipt);
      history.push(`/projects/${data.projectHandle}`);
    } catch (err) {
      console.error(err);
    }
  };

  const modifyDescription = async () => {
    if (!account) {
      message.info(t('design-form-message-please-connect-to-wallet'));
      return;
    }
    if (!project || account.toLowerCase() !== project.projectOwner.toLowerCase()) {
      // console.log(account.toLowerCase() !== project.projectOwner);
      message.info(t('deseign-form-owner-error'));
    }
    const data = form.getFieldsValue(true);
    console.log('raw param', data);
    const id = ethers.utils.formatBytes32String(data.projectHandle);
    const description = encodeObjectToBytes({
      // project description: REQUIRED logo URL
      projectDesc: data.projectDesc,
      website: data.website,
      telegram: data.telegram,
      twitter: data.twitter,
      discord: data.discord,
      payButtonText: data.payButtonText,
      logoUrl: data.logoUrl,
    });
    const tx = await managerContract.modifyDescription(id, description);
    console.log('tx', tx);
    message.success(t('deseign-form-submitted'));
    const receipt = await tx.wait();
    console.log('receipt', receipt);
    history.push(`/projects/${data.projectHandle}`);
  };

  const toggleShowConfirmModal = () => {
    setShowConfirmModal((prevState) => !prevState);
  };

  const getConfirmModal = () => (
    <Modal
      visible={showConfirmModal}
      onOk={toggleShowConfirmModal}
      onCancel={toggleShowConfirmModal}
      wrapClassName={styles.confirmModal}
      width={350}
      centered
      footer={null}>
      <h2>Confirmation</h2>
      <p>Please verify all the information you provided is correct.</p>
      <div className={styles.buttonWrap}>
        <MainButton
          onClick={() => {
            toggleShowConfirmModal();
            if (editing) {
              modifyDescription();
            } else {
              createProject();
            }
          }}>
          Confirm
        </MainButton>
        <MainButton secondary onClick={toggleShowConfirmModal}>
          Cancel
        </MainButton>
      </div>
    </Modal>
  );

  const submit = async () => {
    if (!account) {
      message.info(t('design-form-message-please-connect-to-wallet'));
      return;
    }
    const currentFormValidate = await validateForm();
    const firstIncompleteIndex = findIncomplete();
    const otherIncompleteIndex = findIncompleteExcludeSelf(currentStep);

    // submit: current form validated and no other incomplete form
    if (currentFormValidate && otherIncompleteIndex === -1) {
      // submit
      toggleShowConfirmModal();
      return;
    }

    if (currentFormValidate) {
      goStep(otherIncompleteIndex); // go other incomplete form: current form validated and there is other incomplete form
    } else {
      goStep(firstIncompleteIndex); // go first incomplete form: current form not validated and there is other incomplete form
    }
    message.error(t('deseign-form-not-complete-error'));
  };

  const restoreForm = (data) => {
    form.setFieldsValue({
      projectHandle: data.id,
      projectName: data.projectName,
      tokenName: data.tokenName,
      tokenSymbol: data.tokenSymbol,
      projectDesc: data.description.projectDesc,
      website: data.description.website,
      telegram: data.description.telegram,
      twitter: data.description.twitter,
      discord: data.description.discord,
      payButtonText: data.description.payButtonText,
      logoUrl: data.description.logoUrl,
      fundingDuration: '',
      acceptToken: data.donationTokenName.toLowerCase(),
      fundDistribution: data.donationDistribution.map((item) => ({
        address: item.who,
        ratio: parseInt(item.perMillion, 10) / 10000,
      })),
      minAmountPerTxn: data.minimumDonationAmount,
      hardCap: parseInt(data.donationCap, 10),
      exchangeRate: parseInt(data.price, 10),
      reservedRatio: parseInt(data.reservePerMillion, 10) / 10000,
      tokenReserve: data.reserveDistribution.map((item) => ({
        address: item.who,
        ratio: parseInt(item.perMillion, 10) / 10000,
      })),
      isMintable: data.mintable,
    });
  };

  // render form component according to step
  useEffect(() => {
    // set form state to INPROGRESS on the first render
    if (formState[currentStep] === FORM_STATE.NOTSTARTED) {
      updateArray(setFormState, currentStep, FORM_STATE.INPROGRESS);
    }
    const FormComponent = FORM_CONFIG[currentStep].form;
    setCurrentForm(<FormComponent form={form} editing={editing} />);
  }, [currentStep, editing]);

  // check completeness of forms
  useEffect(() => {
    const index = findIncomplete();
    if (index === -1) {
      setAllComplete(true);
      return;
    }
    setAllComplete(false);
  }, [findIncomplete]);

  // load project data
  useEffect(() => {
    if (project) {
      setEditing(true);
      restoreForm(project);
      setFormState(
        formState.map((_, index) => (index === 0 ? FORM_STATE.INPROGRESS : FORM_STATE.COMPLETED)),
      );
    } else {
      setEditing(false);
    }
  }, [project]);

  // const test = async () => {
  //   console.log(await managerContract.projectRecords(ethers.utils.formatBytes32String('test3')));
  // };

  return (
    <div className={styles.DesignForm}>
      {getConfirmModal()}
      <div className={styles.formNavWrap}>
        {FORM_CONFIG.map((item, index) => (
          <NavItem
            key={t(item.name)}
            name={t(item.name)}
            desc={t(item.desc)}
            active={currentStep === index}
            formState={formState[index]}
            onClick={() => {
              goStep(index);
            }}
          />
        ))}
        <MainButton
          className={classnames(styles.submitButton, { [styles.inactive]: !allComplete })}
          onClick={submit}>
          {t('deseign-submit')}
        </MainButton>
      </div>
      <div className={styles.formInputWrap}>
        <div className={styles.formContainer}>
          {currentForm}
          {/* <MainButton className={styles.formButton} onClick={test}>
            test
          </MainButton> */}
          <div className={styles.buttonWrap}>
            <MainButton
              className={classnames(styles.formButton, { [styles.hide]: currentStep === 0 })}
              onClick={prevStep}>
              {t('project-previous')}
            </MainButton>
            {currentStep === formCount - 1 ? (
              <MainButton
                className={styles.formButton}
                onClick={() => {
                  nextStep();
                  message.success(t('deseign-form-saved'));
                }}>
                {t('project-save')}
              </MainButton>
            ) : (
              <MainButton className={styles.formButton} onClick={nextStep}>
                {t('project-next')}
              </MainButton>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

DesignForm.propTypes = {
  project: PropTypes.objectOf(PropTypes.any),
};

DesignForm.defaultProps = { project: null };

export default DesignForm;
