import { ethers } from 'ethers';
import { TOKENS } from '../constants/addresses';
// truncate address
export const truncAddress = (addr) => {
  if (addr && addr.length === 42) {
    return `${addr.substring(0, 6)}...${addr.substring(addr.length - 4)}`;
  }
  return '';
};

// check if a timestamp is from the past, need to be in millisecond
export const checkTime = (ts) => new Date().getTime() > ts;

// convert decimal number to Hex string
export const toHex = (decimalNum) => `0x${decimalNum.toString(16)}`;

// find the key of addressTarget in addressList
export const getTokenName = (addressTarget, addressList = TOKENS) => {
  const [tokenName] = Object.entries(addressList).find(
    ([, value]) => addressTarget.toLowerCase() === value.toLowerCase(),
  );
  // handle token alias
  return tokenName;
};

// find the value of keyTarget in addressList
export const getTokenAddress = (keyTarget, addressList = TOKENS) =>
  addressList[keyTarget.toUpperCase()];
// encode js object to bytes
export const encodeObjectToBytes = (data) => ethers.utils.toUtf8Bytes(JSON.stringify(data));

// decode js bytes to object
export const decodeBytesToObject = (data) => JSON.parse(ethers.utils.toUtf8String(data));

// time constant
export const TIME_IN_SECOND = {
  SECOND: 1,
  MINUTE: 60,
  HOUR: 60 * 60,
  DAY: 60 * 60 * 24,
  WEEK: 60 * 60 * 24 * 7,
};

// validate GoFundUsManagerType.Project
export const validateProject = (project) =>
  project.projectOwner !== '0x0000000000000000000000000000000000000000';
