import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation, Trans } from 'react-i18next';
import { Form, Input, Select, Slider } from 'antd';
import { ethers } from 'ethers';
import BigNumber from 'bignumber.js';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';
import { SUPPORT_TOKEN } from '../../../../utils/projectsConfig';
import styles from '../Forms.module.scss';

/* eslint-disable react/jsx-props-no-spreading */
const FundingForm = (props) => {
  const { t } = useTranslation();
  const { form, editing } = props;

  const addressValidator = async (_, address) => {
    if (!address) {
      return;
    }
    if (!ethers.utils.isAddress(address)) {
      throw new Error(t('funding-configuration-error-invalid-address'));
    }
  };

  const ratioSumValidator = async (_, distributions) => {
    if (!distributions) {
      return;
    }
    const ratioSum = distributions.reduce((sum, item) => {
      if (!item) {
        return sum;
      }
      return sum + Number(item.ratio);
    }, 0);
    if (ratioSum > 100) {
      throw new Error(t('funding-configuration-error-ratio-exceed'));
    }
  };

  const minAmountPerTxnValidator = async (_, amount) => {
    if (!amount) {
      return;
    }
    if (Number(amount) < 0.01) {
      throw new Error(t('funding-configuration-form-item-minimum-amount-alert-message-min'));
    }
  };

  const fundingDurationValidator = async (_, amount) => {
    if (!amount) {
      return;
    }
    if (Number(amount) < 3) {
      throw new Error(t('funding-configuration-duration-error'));
    }
  };

  const exchangeRateValidator = async (_, amount) => {
    if (!amount) {
      return;
    }
    if (BigNumber(amount).isGreaterThan(BigNumber('1000000000000'))) {
      throw new Error(t('funding-configuration-exchange-rate-max-error'));
    }
  };

  const getMinAmountPerTxn = (token) => {
    let value;
    switch (token) {
      case SUPPORT_TOKEN.BNB:
        value = '0.01';
        break;
      case SUPPORT_TOKEN.BUSD:
        value = '1';
        break;
      default:
    }
    return value;
  };

  const onAcceptTokenChange = (value) => {
    form.setFieldsValue({ minAmountPerTxn: getMinAmountPerTxn(value) });
  };

  const defaultToken = SUPPORT_TOKEN.BNB;

  const initialValues = {
    acceptToken: defaultToken,
    minAmountPerTxn: getMinAmountPerTxn(defaultToken),
  };

  return (
    <div className={styles.DetailForm}>
      <div className={styles.titleWrap}>
        <h2>{t('deseign-form-config-Funding-configurations')}</h2>
        <p>{t('funding-configuration-explanation')}</p>
      </div>

      <Form layout="vertical" form={form} initialValues={initialValues}>
        <Form.Item
          label={t('funding-configuration-form-item-accepted-currency')}
          name="acceptToken"
          rules={[
            {
              required: true,
              message: t('funding-configuration-form-item-accepted-currency-alert-message'),
            },
          ]}
          tooltip={t('funding-configuration-form-item-accepted-currency-tooltip')}>
          <Select onChange={onAcceptTokenChange} disabled={editing}>
            {Object.values(SUPPORT_TOKEN).map((item) => (
              <Select.Option key={item} value={item}>
                {item.toUpperCase()}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item
          label={t('funding-configuration-form-item-minimum-amount')}
          name="minAmountPerTxn"
          rules={[
            {
              required: true,
              pattern: /^([0-9]+[.])?[0-9]+$/,
              message: t('funding-configuration-form-item-minimum-amount-alert-message'),
            },
            {
              validator: minAmountPerTxnValidator,
            },
          ]}
          tooltip={t('funding-configuration-form-item-minimum-amount-tooltip')}>
          <Input
            placeholder={t('funding-configuration-form-item-minimum-amount-placeholder')}
            disabled={editing}
          />
        </Form.Item>

        <Form.Item
          label={t('funding-configuration-form-item-hard-cap')}
          name="hardCap"
          rules={[
            {
              pattern: /^[0-9]+$/,
              message: t('funding-configuration-form-item-hard-cap-alert-message'),
            },
          ]}
          tooltip={t('funding-configuration-form-item-hard-cap-tooltip')}>
          <Input
            placeholder={t('funding-configuration-form-item-hard-cap-placeholder')}
            disabled={editing}
          />
        </Form.Item>

        <Form.Item
          label={t('funding-configuration-form-item-funding-duration')}
          name="fundingDuration"
          tooltip={t('funding-configuration-form-item-funding-duration-tooltip')}
          rules={[
            {
              pattern: /^[0-9]+$/,
              message: t('funding-configuration-form-item-funding-duration-alert-message'),
            },
            {
              validator: fundingDurationValidator,
            },
          ]}>
          <Input
            placeholder={t('funding-configuration-form-item-funding-duration-placeholder')}
            suffix={
              <p className={styles.suffix}>
                {t('funding-configuration-form-item-funding-duration-suffix')}
              </p>
            }
            disabled={editing}
          />
        </Form.Item>

        <Form.Item
          label={t('funding-configuration-form-item-token-name')}
          tooltip={t('funding-configuration-form-item-token-name-tooltip')}
          rules={[
            {
              required: true,
              message: t('funding-configuration-form-item-token-name-required-alert-message'),
            },
            {
              type: 'string',
              min: 3,
              max: 25,
              message: t('funding-configuration-form-item-token-name-type-alert-message'),
            },
            {
              // pattern: /^[^\s_0-9][a-zA-Z0-9_\s]+[^\s]$/,
              pattern: /^[a-zA-Z0-9][a-zA-Z0-9_ ]+[a-zA-Z0-9]$/,
              message: t('funding-configuration-form-item-token-name-pattern-alert-message'),
            },
          ]}
          name="tokenName">
          <Input
            placeholder={t('funding-configuration-form-item-token-name-placeholder')}
            disabled={editing}
          />
        </Form.Item>

        <Form.Item
          label={t('funding-configuration-form-item-token-symbol')}
          tooltip={t('funding-configuration-form-item-token-symbol-tooltip')}
          rules={[
            {
              required: true,
              message: t('funding-configuration-form-item-token-symbol-required-alert-message'),
            },
            {
              type: 'string',
              min: 3,
              max: 20,
              message: t('funding-configuration-form-item-token-symbol-type-alert-message'),
            },
            {
              pattern: /^[a-zA-Z0-9]+$/,
              message: t('funding-configuration-form-item-token-symbol-pattern-alert-message'),
            },
          ]}
          name="tokenSymbol">
          <Input
            placeholder={t('funding-configuration-form-item-token-symbol-placeholder')}
            disabled={editing}
          />
        </Form.Item>
        <Form.Item
          label={t('funding-configuration-form-item-token-exchange-rate')}
          // shouldUpdate={(prevValues, currentValues) =>
          //   prevValues.acceptToken !== currentValues.acceptToken
          // }
          tooltip={t('funding-configuration-form-item-token-exchange-rate-tooltip')}
          dependencies={['acceptToken', 'tokenSymbol']}>
          {({ getFieldValue }) => (
            <>
              <Form.Item
                name="exchangeRate"
                noStyle
                rules={[
                  {
                    pattern: /^[0-9]+$/,
                    message: t('funding-configuration-form-item-token-exchange-rate-invalid-error'),
                  },
                  {
                    validator: exchangeRateValidator,
                  },
                ]}>
                {/* <InputNumber
                  min={0}
                  // max={1000000000000}
                  style={{
                    width: '50%',
                  }}
                  placeholder={t('funding-configuration-form-item-token-exchange-rate-placeholder')}
                /> */}
                <Input
                  style={{
                    width: '50%',
                  }}
                  disabled={editing}
                  placeholder="1,000,000"
                />
              </Form.Item>
              <span className={styles.antFormText}>
                {getFieldValue('tokenSymbol')
                  ? getFieldValue('tokenSymbol')
                  : t('funding-configuration-form-item-exchange-rate-project-token')}
                /{getFieldValue('acceptToken').toUpperCase()}
              </span>
            </>
          )}
        </Form.Item>

        <Form.Item
          label={t('funding-configuration-form-item-funding-distribution')}
          style={{ marginBottom: 0 }}
          tooltip={
            <Trans i18nKey="funding-configuration-form-item-funding-distribution-tooltip">
              <p>
                Distribute available funds to other wallets. Use this to pay contributors,
                charities, and more. Funds are distributed whenever a withdrawal is made from your
                project.
                <br />
                <br />
                By default, all unallocated funds can be distributed to the project owner&apos;s
                wallet.
              </p>
            </Trans>
          }>
          <Form.List
            name="fundDistribution"
            rules={[
              {
                validator: ratioSumValidator,
              },
            ]}>
            {(fields, { add, remove }, { errors }) => (
              <>
                {fields.map(({ key, name, ...restField }) => (
                  <span key={key} className={styles.addressRatio}>
                    <Form.Item
                      {...restField}
                      name={[name, 'address']}
                      rules={[
                        {
                          required: true,
                          message: t(
                            'funding-configuration-form-item-missing-address-alert-message',
                          ),
                        },
                        { validator: addressValidator },
                      ]}>
                      <Input placeholder={t('detail-page-address')} disabled={editing} />
                    </Form.Item>
                    <Form.Item
                      {...restField}
                      name={[name, 'ratio']}
                      rules={[
                        {
                          required: true,
                          message: t('funding-configuration-form-item-missing-ratio-alert-message'),
                        },
                        {
                          pattern: /^[0-9]+$/,
                          message: t(
                            'funding-configuration-form-item-missing-ratio--invalid-alert-message',
                          ),
                        },
                      ]}>
                      <Input
                        placeholder={t(
                          'funding-configuration-form-item-funding-distribution-ratio',
                        )}
                        suffix="%"
                        disabled={editing}
                      />
                    </Form.Item>
                    <MinusCircleOutlined onClick={() => remove(name)} />
                  </span>
                ))}
                <Form.Item style={{ marginBottom: 0 }}>
                  <Form.ErrorList errors={errors} />
                  <span className={styles.addButton} onClick={() => (editing ? null : add())}>
                    <PlusOutlined /> {t('funding-configuration-form-item-add-aaddress')}
                  </span>
                </Form.Item>
              </>
            )}
          </Form.List>
        </Form.Item>

        <Form.Item
          label={t('funding-configuration-form-item-token-reserved')}
          style={{ marginBottom: 0 }}
          tooltip={
            <span>
              <p>{t('funding-configuration-form-item-token-reserved-tooltip')}</p>
            </span>
          }>
          <Form.Item name="reservedRatio">
            <Slider
              marks={{
                0: 0,
                50: '50%',
                100: '100%',
              }}
              disabled={editing}
            />
          </Form.Item>
          <Form.List
            name="tokenReserve"
            rules={[
              {
                validator: ratioSumValidator,
              },
            ]}>
            {(fields, { add, remove }, { errors }) => (
              <>
                {fields.map(({ key, name, ...restField }) => (
                  <span key={key} className={styles.addressRatio}>
                    <Form.Item
                      {...restField}
                      name={[name, 'address']}
                      rules={[
                        {
                          required: true,
                          message: t(
                            'funding-configuration-form-item-missing-address-alert-message',
                          ),
                        },
                        { validator: addressValidator },
                      ]}>
                      <Input placeholder={t('detail-page-address')} disabled={editing} />
                    </Form.Item>
                    <Form.Item
                      {...restField}
                      name={[name, 'ratio']}
                      rules={[
                        {
                          required: true,
                          message: t('funding-configuration-form-item-missing-ratio-alert-message'),
                        },
                        {
                          pattern: /^[0-9]+$/,
                          message: t('funding-configuration-form-item-missing-ratio-alert-message'),
                        },
                      ]}>
                      <Input
                        placeholder={t(
                          'funding-configuration-form-item-funding-distribution-ratio',
                        )}
                        suffix="%"
                        disabled={editing}
                      />
                    </Form.Item>
                    <MinusCircleOutlined onClick={() => remove(name)} />
                  </span>
                ))}
                <Form.Item style={{ marginBottom: 0 }}>
                  <Form.ErrorList errors={errors} />
                  <span className={styles.addButton} onClick={() => (editing ? null : add())}>
                    <PlusOutlined /> {t('funding-configuration-form-item-add-aaddress')}
                  </span>
                </Form.Item>
              </>
            )}
          </Form.List>
        </Form.Item>
      </Form>
    </div>
  );
};
FundingForm.propTypes = {
  form: PropTypes.objectOf(PropTypes.any).isRequired,
  editing: PropTypes.bool,
};

FundingForm.defaultProps = { editing: false };
export default FundingForm;
