import { createContext, useContext, useEffect, useState } from 'react';
import { PropTypes } from 'prop-types';
import { useWallet } from './useWallet';

const updateContext = createContext();

export const useUpdate = () => useContext(updateContext);

const useProvideUpdate = () => {
  const [blockNumber, setBlockNumber] = useState();
  const { ethersProvider } = useWallet();

  useEffect(() => {
    const updateBlockNumber = (block) => {
      setBlockNumber(block);
    };
    ethersProvider.on('block', updateBlockNumber);
    return () => {
      ethersProvider.off('block', updateBlockNumber);
    };
  }, [ethersProvider]);

  return { updateByBlockNumber: blockNumber };
};

export function ProvideUpdate({ children }) {
  const update = useProvideUpdate();
  return <updateContext.Provider value={update}>{children}</updateContext.Provider>;
}
ProvideUpdate.propTypes = {
  children: PropTypes.node.isRequired,
};
