import React from 'react';
import { message } from 'antd';
import { utils } from 'ethers';
import PropTypes from 'prop-types';
import numeral from 'numeral';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useTranslation } from 'react-i18next';

import styles from './styles.module.scss';

import { useAccountNativeBalance, useAccountTokenBalance } from '../../hooks/useAccountBalance';
import { TOKENS } from '../../constants/addresses';
import { useWallet } from '../../hooks/useWallet';

const WalletDetail = ({ toggleWalletDetailModal }) => {
  const { account, disconnect } = useWallet();
  const { data: bnbBalance } = useAccountNativeBalance();
  const { data: busdBalance } = useAccountTokenBalance(TOKENS.BUSD);
  const { t } = useTranslation();

  return (
    <div className="popup">
      <div className="title">
        <p>{t('wallet-detail-your-wallet')}</p>
        <img
          src="/static/images/close.png"
          alt="close"
          onClick={() => {
            toggleWalletDetailModal();
          }}
        />
      </div>
      <div className={styles.walletInfoWrapper}>
        <h3>Address</h3>
        <div className={styles.walletAddressWrapper}>
          <div className={styles.address}>{account}</div>
          <CopyToClipboard
            text={account}
            onCopy={() => {
              message.success(t('wallet-detail-copied-to-clipboard'));
            }}>
            <div className={styles.copyButton}>{t('wallet-detail-copy')}</div>
          </CopyToClipboard>
        </div>
        <div className={styles.balanceRow}>
          <div className={styles.tokenName}>
            <img src="/static/images/tokens/bnb.svg" alt="bnb" />
            {`BNB ${t('wallet-detail-balance')}`}
          </div>
          <div className={styles.tokenBalance}>
            {numeral(utils.formatEther(bnbBalance)).format('0,0.[00]')} BNB
          </div>
        </div>
        <div className={styles.balanceRow}>
          <div className={styles.tokenName}>
            <img src="/static/images/tokens/busd.svg" alt="busd" />
            {`BUSD ${t('wallet-detail-balance')}`}
          </div>
          <div className={styles.tokenBalance}>
            {numeral(utils.formatEther(busdBalance)).format('0,0.[00]')} BUSD
          </div>
        </div>
      </div>
      <div className={styles.bscscanLink}>
        <a
          href={`https://bscscan.com/address/${account}`}
          target="_blank"
          rel="noopener noreferrer">
          {t('wallet-detail-view-bscscan')}
          <img src="/static/images/icons/rightBracket.svg" alt="busd" />
        </a>
      </div>
      <div
        className={styles.disconnectButton}
        onClick={() => {
          toggleWalletDetailModal();
          disconnect();
        }}>
        {t('wallet-detail-disconnect-wallet')}
      </div>
    </div>
  );
};

export default WalletDetail;

WalletDetail.propTypes = {
  toggleWalletDetailModal: PropTypes.func.isRequired,
};
