import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { CheckOutlined, EllipsisOutlined } from '@ant-design/icons';
import styles from './NavItem.module.scss';
import FORM_STATE from '../DesignForm/formState';

const NavItem = (props) => {
  const { name, desc, active, formState, onClick } = props;

  const getIcon = () => {
    let icon;
    switch (formState) {
      case FORM_STATE.COMPLETED:
        icon = <CheckOutlined />;
        break;
      case FORM_STATE.INPROGRESS:
        icon = <EllipsisOutlined />;
        break;
      default:
    }
    return icon;
  };

  return (
    <div
      className={classnames(
        styles.NavItem,
        { [styles.inprogress]: formState === FORM_STATE.INPROGRESS },
        { [styles.completed]: formState === FORM_STATE.COMPLETED },
        { [styles.active]: active },
      )}
      onClick={onClick}>
      <div className={styles.textWrap}>
        <h2>{name}</h2>
        <p>{desc}</p>
      </div>
      <div className={styles.stateWrap}>{getIcon()}</div>
    </div>
  );
};

NavItem.propTypes = {
  name: PropTypes.string.isRequired,
  desc: PropTypes.string.isRequired,
  active: PropTypes.bool,
  formState: PropTypes.oneOf(Object.values(FORM_STATE)),
  onClick: PropTypes.func,
};

NavItem.defaultProps = { active: false, formState: FORM_STATE.NOTSTARTED, onClick: null };

export default NavItem;
