import React from 'react';
import { useTranslation } from 'react-i18next';
import PropTypes from 'prop-types';
import { Form, Input } from 'antd';
import { useAllProjectIds } from '../../../../hooks/useProjectInfo';
import styles from '../Forms.module.scss';

const DetailForm = (props) => {
  const { t } = useTranslation();
  const { form, editing } = props;
  // const managerContract = useGoFundUsManagerContract();
  const { ids } = useAllProjectIds();
  const initialValues = {};

  const projectHandleValidator = async (_, handle) => {
    if (!handle || editing) {
      return;
    }
    if (ids.includes(handle)) {
      throw new Error(t('project-detail-form-project-handle-existed'));
    }
  };
  // const projectHandleValidator = null;

  const logoUrlValidator = async (_, url) =>
    new Promise((resolve, reject) => {
      if (!url) {
        resolve();
      }
      const img = new Image();
      img.onload = () => {
        if (img.width !== img.height || img.width < 300 || img.width > 600) {
          reject(new Error(t('project-detail-form-logo-size-error')));
        }
        resolve();
      };

      img.onerror = () => {
        reject(new Error(t('project-detail-form-item-logo-url-alert-message-invalid-url')));
      };
      img.src = url;
    });

  return (
    <div className={styles.DetailForm}>
      <div className={styles.titleWrap}>
        <h2>{t('deseign-form-config-project-details')}</h2>
        <p>{t('project-detail-explanation')}</p>
      </div>

      <Form layout="vertical" form={form} initialValues={initialValues}>
        <Form.Item
          label={t('project-detail-form-item-project-name')}
          name="projectName"
          rules={[
            {
              required: true,
              message: t('project-detail-form-item-project-name-alert-message'),
            },
            {
              type: 'string',
              min: 3,
              max: 25,
              message: t('project-detail-form-item-project-name-length-alert-message'),
            },
            {
              // pattern: /^[^\s_0-9][a-zA-Z0-9_\s]+[^\s]$/,
              pattern: /^[a-zA-Z0-9][a-zA-Z0-9_ ]+[a-zA-Z0-9]$/,
              message: t('funding-configuration-form-item-token-name-pattern-alert-message'),
            },
          ]}>
          <Input
            placeholder={t('project-detail-form-item-project-name-placeholder')}
            disabled={editing}
          />
        </Form.Item>
        <Form.Item
          label={t('project-detail-form-item-project-handle')}
          name="projectHandle"
          validateTrigger={['onChange', 'onBlur']}
          rules={[
            {
              required: true,
              message: t('project-detail-form-item-project-handle-alert-message'),
            },
            {
              type: 'string',
              min: 3,
              max: 25,
              message: t('project-detail-form-item-project-handle-length-alert-message'),
            },
            {
              // pattern: /^[^\s_0-9][a-zA-Z0-9_\s]+[^\s]$/,
              pattern: /^[a-zA-Z0-9][a-zA-Z0-9_]+[a-zA-Z0-9]$/,
              message: t('funding-configuration-form-item-token-name-pattern-alert-message'),
            },
            {
              validator: projectHandleValidator,
              validateTrigger: 'onBlur',
            },
          ]}
          tooltip={t('project-detail-form-item-project-handle-tooltip')}>
          <Input
            prefix="@"
            placeholder={t('project-detail-form-item-project-handle-placeholder')}
            disabled={editing}
          />
        </Form.Item>
        <Form.Item
          label={t('project-detail-form-item-project-description')}
          name="projectDesc"
          rules={[
            {
              max: 1000,
              message: t('project-detail-form-item-project-description-alert-message'),
            },
          ]}>
          <Input placeholder={t('project-detail-form-item-project-description-placeholder')} />
        </Form.Item>
        <Form.Item
          label="Website"
          name="website"
          tooltip={t('project-detail-form-item-website-tooltip')}
          rules={[
            {
              type: 'url',
              message: t('project-detail-form-item-logo-url-alert-message-invalid-url'),
            },
          ]}>
          <Input placeholder="https://example-project.com/" />
        </Form.Item>
        <Form.Item
          label="Telegram"
          name="telegram"
          tooltip={t('project-detail-form-item-telegram-tooltip')}>
          <Input prefix="@" placeholder="exampleChannel" />
        </Form.Item>
        <Form.Item
          label="Twitter"
          name="twitter"
          tooltip={t('project-detail-form-item-twitter-tooltip')}>
          <Input prefix="@" placeholder="exampleTwitter" />
        </Form.Item>
        <Form.Item
          label="Discord"
          name="discord"
          tooltip={t('project-detail-form-item-discord-tooltip')}
          rules={[
            {
              type: 'url',
              message: t('project-detail-form-item-logo-url-alert-message-invalid-url'),
            },
          ]}>
          <Input placeholder="https://discord.com/12345678" />
        </Form.Item>
        <Form.Item
          label={t('project-detail-form-item-pay-button-text')}
          name="payButtonText"
          tooltip={t('project-detail-form-item-pay-button-text-tooltip')}
          rules={[
            {
              min: 2,
              max: 10,
              message: t('project-detail-form-item-pay-button-text-alert-message'),
            },
          ]}>
          <Input placeholder="Pay" />
        </Form.Item>
        <Form.Item
          label={t('project-detail-form-item-logo-url')}
          name="logoUrl"
          validateTrigger={['onChange', 'onBlur']}
          rules={[
            {
              required: true,
              message: t('project-detail-form-item-logo-url-alert-message-no-url'),
            },
            {
              type: 'url',
              message: t('project-detail-form-item-logo-url-alert-message-invalid-url'),
            },
            {
              validateTrigger: ['onBlur'],
              validator: logoUrlValidator,
            },
          ]}
          tooltip={t('project-detail-form-item-logo-url-tooltip')}>
          <Input placeholder="https://example-project.com/logo.png" />
        </Form.Item>
      </Form>
    </div>
  );
};

DetailForm.propTypes = {
  form: PropTypes.objectOf(PropTypes.any).isRequired,
  editing: PropTypes.bool,
};

DetailForm.defaultProps = { editing: false };

export default DetailForm;
