import { useEffect, useState } from 'react';

import { useWallet } from './useWallet';
import { useTokenContract } from './useContract';

export const useAccountNativeBalance = (optionalAccount = null) => {
  const { ethersProvider, account } = useWallet();
  const [data, setData] = useState(0);
  const [loading, setLoading] = useState(false);

  useEffect(async () => {
    setLoading(true);
    try {
      const balance = await ethersProvider.getBalance(optionalAccount || account);
      setData(balance);
    } catch (err) {
      console.error(err);
      setData(0);
    } finally {
      setLoading(false);
    }
  }, []);

  return { data, loading };
};

export const useAccountTokenBalance = (token, optionalAccount = null) => {
  const tokenContract = useTokenContract(token);
  const { account } = useWallet();
  const [data, setData] = useState(0);
  const [loading, setLoading] = useState(false);

  useEffect(async () => {
    setLoading(true);
    try {
      const balance = await tokenContract.balanceOf(optionalAccount || account);
      setData(balance);
    } catch (err) {
      console.error(err);
      setData(0);
    } finally {
      setLoading(false);
    }
  }, []);

  return { data, loading };
};
