import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { wallets } from './config';
import './styles.scss';
import { useWallet } from '../../hooks/useWallet';

const WalletList = ({ toggleWalletListModal }) => {
  const { t } = useTranslation();
  const { connect } = useWallet();
  return (
    <div className="popup">
      <div className="title">
        <p>{t('wallet-connect-your-wallet')}</p>
        <img
          src="/static/images/close.png"
          alt="close"
          onClick={() => {
            toggleWalletListModal();
          }}
        />
      </div>
      {wallets.map((item) => (
        <div
          key={item.name}
          className="walletCard"
          onClick={() => {
            connect('metamask');
            toggleWalletListModal();
          }}>
          <p>{item.name}</p>
          <img src={`/static/images/wallets/${item.type.toLowerCase()}.png`} alt="wallet" />
        </div>
      ))}
    </div>
  );
};
export default WalletList;

WalletList.propTypes = {
  toggleWalletListModal: PropTypes.func.isRequired,
};
