import React, { useState, useEffect, useContext, createContext, useCallback } from 'react';
import detectEthereumProvider from '@metamask/detect-provider';
import PropTypes from 'prop-types';
import { ethers } from 'ethers';
import { toHex } from '../utils/utils';

import { CHAIN_INFO } from '../utils/chainConfig';

const DEFAULT_RPC = CHAIN_INFO.rpc[0];

export const WALLET_LIST = {
  METAMASK: 'metamask',
  WALLET_CONNECT: 'walletconnect',
};

const walletContext = createContext();

export const useWallet = () => useContext(walletContext);

// Provider hook that creates auth object and handles state
function useProvideWallet() {
  const [account, setAccount] = useState(null);
  const [ethersProvider, setEthersProvider] = useState(
    new ethers.providers.JsonRpcProvider(DEFAULT_RPC),
  );

  const resetDefault = useCallback(() => {
    setAccount(null);
    setEthersProvider(new ethers.providers.JsonRpcProvider(DEFAULT_RPC));
  }, []);

  // connect to chain
  const connect = useCallback(async (walletType = WALLET_LIST.METAMASK) => {
    // helper: verify current chain with target
    const verifyChain = async (provider) => {
      const chainId = parseInt(await provider.request({ method: 'eth_chainId' }), 16);
      // console.log("current chainId:", chainId);
      return chainId === CHAIN_INFO.chainId;
    };
    // handle chain change
    const handleChainChanged = async (provider) => {
      // add/switch chain to target chain
      if (!(await verifyChain(provider))) {
        const networkData = {
          chainId: toHex(CHAIN_INFO.chainId),
          chainName: CHAIN_INFO.name,
          nativeCurrency: CHAIN_INFO.nativeCurrency,
          rpcUrls: CHAIN_INFO.rpc,
          blockExplorerUrls: [CHAIN_INFO.explorers[0].url],
        };
        console.log('networkData', networkData);
        await provider.request({
          method: 'wallet_addEthereumChain',
          params: [networkData, null],
        });
        // verify chain switch
        if (!(await verifyChain(provider))) {
          throw new Error('Chain switch failed');
        }
      }
    };

    try {
      const provider = await detectEthereumProvider();
      if (walletType !== WALLET_LIST.METAMASK) {
        // do something here for other wallet
      }
      // handle no provider
      if (!provider) {
        console.log('No provider detected');
        return;
      }
      // TODO: handle multiple providers or verify wallet is metamask
      if (provider !== window.ethereum) {
        console.log('Multiple wallets detected');
        return;
      }
      // change chain
      await handleChainChanged(provider);
      // get account
      const accounts = await provider.request({ method: 'eth_requestAccounts' });

      if (accounts.length === 0) {
        console.log('no account');
        return;
      }

      // set account
      setAccount(accounts[0].toLowerCase());

      // set ethersProvider
      setEthersProvider(new ethers.providers.Web3Provider(provider));

      // listen to chainChanged
      provider.on('chainChanged', () => {
        window.location.reload();
      });

      // listen to accountsChanged
      provider.on('accountsChanged', () => {
        window.location.reload();
      });
      // set auto connect
      sessionStorage.autoConnect = '1';
      sessionStorage.walletType = walletType;
    } catch (err) {
      console.error(err);
    }
  }, []);

  const disconnect = () => {
    resetDefault();
    sessionStorage.clear();
  };

  useEffect(() => {
    if (sessionStorage.autoConnect && sessionStorage.autoConnect === '1') {
      connect(sessionStorage.walletType);
    }
  }, []);

  return {
    account,
    connect,
    disconnect,
    ethersProvider,
  };
}

export function ProvideWallet({ children }) {
  const wallet = useProvideWallet();
  return <walletContext.Provider value={wallet}>{children}</walletContext.Provider>;
}
ProvideWallet.propTypes = {
  children: PropTypes.node.isRequired,
};
