import React, { useCallback, useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, NavLink } from 'react-router-dom';
import classnames from 'classnames';
import { MenuOutlined } from '@ant-design/icons';
import { Menu, Dropdown, Modal } from 'antd';

import MainContainer from '../UI/MainContainer/MainContainer';
import MainButton from '../MainButton/MainButton';
import WalletList from '../WalletList';
import { truncAddress } from '../../utils/utils';
import { useWallet } from '../../hooks/useWallet';
import WalletDetail from '../WalletDetail';

import styles from './Header.module.scss';

const langs = {
  en: { nativeName: 'English' },
  ar: { nativeName: 'عربي' },
  de: { nativeName: 'Deutsch' },
  el: { nativeName: 'Eλληνικά' },
  es: { nativeName: 'Español' },
  fi: { nativeName: 'Suomi' },
  fr: { nativeName: 'Français' },
  zh: { nativeName: '简体中文' },
  // zh_HANT: { nativeName: '繁體中文' },
  ja: { nativeName: '日本語' },
  pt: { nativeName: 'Português' },
  it: { nativeName: 'Italiano' },
  ro: { nativeName: 'Română' },
  ko: { nativeName: '한국의' },
  ru: { nativeName: 'Русский' },
  bn: { nativeName: 'বাংলা' },
  // hr: { nativeName: 'hrvatski' },
  hi: { nativeName: 'हिन्दी或हिंदी' },
  hu: { nativeName: 'Magyar' },
  vi: { nativeName: 'Tiếng Việt' },
};

const Header = () => {
  const [menuCollapsed, setMenuCollapsed] = useState(true);
  const [walletListModalStatus, setWalletListModalStatus] = useState(false);
  const [walletDetailModalStatus, setWalletDetailModalStatus] = useState(false);
  const { i18n } = useTranslation();
  const [currentLang, setCurrentLang] = useState('English');
  // i18n.resolvedLanguage ? langs[i18n.resolvedLanguage].nativeName : 'English',
  const wallet = useWallet();

  useEffect(() => {
    setCurrentLang(langs[i18n.resolvedLanguage].nativeName);
  }, [i18n.resolvedLanguage]);

  const toggleMenu = () => {
    setMenuCollapsed(!menuCollapsed);
  };

  const toggleWalletListModal = useCallback(() => {
    setWalletListModalStatus(!walletListModalStatus);
  });

  const toggleWalletDetailModal = useCallback(() => {
    setWalletDetailModalStatus(!walletDetailModalStatus);
  });
  const langMenu = (
    <Menu>
      {Object.keys(langs).map((lang) => (
        <Menu.Item
          key={lang}
          type="submit"
          onClick={() => {
            i18n.changeLanguage(lang);
            setCurrentLang(langs[lang].nativeName);
          }}>
          {langs[lang].nativeName}
        </Menu.Item>
      ))}
    </Menu>
  );

  return (
    <div className={styles.Header}>
      <MainContainer className={styles.headerContainer}>
        <Link to="/">
          <img src="/static/images/header/logo.png" alt="logo" className={styles.logo} />
        </Link>
        <MenuOutlined onClick={toggleMenu} className={styles.hamburger} />
        <div
          className={styles.menuWrap}
          style={{ display: window.innerWidth < 765 && menuCollapsed ? 'none' : 'flex' }}>
          <div className={styles.navLinkWrap}>
            <NavLink
              to="/projects"
              className={(isActive) =>
                classnames(styles.navLink, { [styles.navLinkActive]: isActive })
              }>
              <h2>{i18n.t('home-header-tabs-projects')}</h2>
            </NavLink>
            <NavLink
              to="/design"
              className={(isActive) =>
                classnames(styles.navLink, { [styles.navLinkActive]: isActive })
              }>
              <h2>{i18n.t('home-header-tabs-Design')}</h2>
            </NavLink>
          </div>
          {wallet && wallet.account ? (
            <MainButton className={styles.connectButton} onClick={toggleWalletDetailModal}>
              {truncAddress(wallet.account)}
            </MainButton>
          ) : (
            <MainButton className={styles.connectButton} onClick={toggleWalletListModal}>
              {i18n.t('home-header-button-ConnectWallet')}
            </MainButton>
          )}

          <Modal
            title={null}
            visible={walletListModalStatus}
            onOk={toggleWalletListModal}
            onCancel={toggleWalletListModal}
            wrapClassName={styles.walletModalWrapper}
            width={350}
            centered
            closable={false}
            footer={null}>
            <WalletList toggleWalletListModal={toggleWalletListModal} />
          </Modal>
          <Modal
            title={null}
            visible={walletDetailModalStatus}
            onOk={toggleWalletDetailModal}
            onCancel={toggleWalletDetailModal}
            wrapClassName={styles.walletModalWrapper}
            width={400}
            centered
            closable={false}
            footer={null}>
            <WalletDetail toggleWalletDetailModal={toggleWalletDetailModal} />
          </Modal>
        </div>
        <Dropdown overlay={langMenu}>
          <div className={styles.langButton} onClick={(e) => e.preventDefault()}>
            {currentLang}
          </div>
        </Dropdown>
      </MainContainer>
    </div>
  );
};

export default Header;
