import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import classnames from 'classnames';
import styles from './ProjectIntro.module.scss';
import BorderedCard from '../../UI/BorderedCard/BorderedCard';

const ProjectIntro = (props) => {
  const { item, className } = props;
  const { description } = item;
  const { t } = useTranslation();
  return (
    <div className={classnames(styles.ProjectIntro, className)}>
      <BorderedCard className={styles.introCard}>
        <img
          src={description && description.logoUrl ? description.logoUrl : '/static/images/logo.png'}
          alt="logo"
          className={styles.logo}
        />
        <div className={styles.textWrap}>
          <h2>{item.projectName}</h2>
          {description && (
            <>
              <p>{description.projectDesc}</p>
              <div className={styles.linkWrap}>
                {description.website && (
                  <a href={description.website} target="_blank" rel="noopener noreferrer">
                    <span>
                      <img src="/static/images/projectDetail/projectIntro/website.svg" alt="logo" />{' '}
                      Website
                    </span>
                  </a>
                )}

                {description.discord && (
                  <a href={description.discord} target="_blank" rel="noopener noreferrer">
                    <span>
                      <img src="/static/images/projectDetail/projectIntro/discord.svg" alt="logo" />{' '}
                      Discord
                    </span>
                  </a>
                )}

                {description.twitter && (
                  <a
                    href={`https://twitter.com/${description.twitter}`}
                    target="_blank"
                    rel="noopener noreferrer">
                    <span>
                      <img src="/static/images/projectDetail/projectIntro/twitter.svg" alt="logo" />
                      {`@${description.twitter}`}
                    </span>
                  </a>
                )}
                {description.telegram && (
                  <a
                    href={`https://t.me/${description.telegram}`}
                    target="_blank"
                    rel="noopener noreferrer">
                    <span>
                      <img
                        src="/static/images/projectDetail/projectIntro/telegram.svg"
                        alt="logo"
                      />{' '}
                      {`@${description.telegram}`}
                    </span>
                  </a>
                )}
              </div>
            </>
          )}
        </div>
        {item.isOwner ? (
          <Link to={`/design/${item.id}`}>
            <div className={styles.editButton}>
              <span>{t('project-action-edit')}</span>
              <img src="static/images/icons/rightArrow.png" alt="arrow" />
            </div>
          </Link>
        ) : null}
      </BorderedCard>
    </div>
  );
};

ProjectIntro.propTypes = {
  item: PropTypes.objectOf(PropTypes.any).isRequired,
  className: PropTypes.string,
};

ProjectIntro.defaultProps = { className: '' };

export default ProjectIntro;
