import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { List } from 'antd';
import classnames from 'classnames';
import ProjectItem from '../ProjectItem/ProjectItem';
import styles from './ProjectList.module.scss';

const TOKEN_PRICE = {
  BNB: 400,
  BUSD: 1,
};

const ProjectList = (props) => {
  const { items, className, sort } = props;
  const [dataSource, setDataSource] = useState([]);
  useEffect(() => {
    if (sort) {
      setDataSource(
        items.sort(
          (a, b) =>
            parseFloat(b.totalDonation) * TOKEN_PRICE[b.donationTokenName.toUpperCase()] -
            parseFloat(a.totalDonation) * TOKEN_PRICE[a.donationTokenName.toUpperCase()],
        ),
      );
    } else {
      setDataSource(items);
    }
  });
  return (
    <div className={classnames(styles.ProjectList, className)}>
      <List
        grid={{
          gutter: 24,
          xs: 1,
          sm: 2,
          md: 2,
          lg: 3,
          xl: 3,
          xxl: 3,
        }}
        dataSource={dataSource}
        renderItem={(item) => (
          <List.Item>
            <Link to={`/projects/${item.id}`}>
              <ProjectItem item={item} />
            </Link>
          </List.Item>
        )}
      />
    </div>
  );
};
// const ItemType = PropTypes.shape({
//   id: PropTypes.number
// })
ProjectList.propTypes = {
  items: PropTypes.arrayOf(PropTypes.object),
  className: PropTypes.string,
  sort: PropTypes.bool,
};

ProjectList.defaultProps = {
  items: [],
  className: '',
  sort: false,
};

export default ProjectList;
